
#services{
  width:100%;
  height: auto;
  padding: 36px 5% 0 5%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  margin: auto;
  z-index: 2;
  position: relative;
  background-color: #fff;
  background-size: cover;
  background-position: bottom;
  background-repeat: no-repeat;
  background-attachment: fixed;
  /* background-color: rgba(55, 75, 99); */
  text-align: center;
}

.title {
  margin-bottom: 80px;
  text-align: center;
}

.pre-title-text {
  margin-bottom: -7px;
  font-size: 11px;
  font-weight: 700;
  font-family: 'Playfair Display', serif;
  letter-spacing: 1px;
  color: rgb(155, 155, 155);
  text-transform: uppercase;
}

.title-text {
  font-size: 50px;
  font-family: 'Playfair Display', serif;
  /* color: rgb(74, 74 , 74); */
  color: rgb(155, 155, 155);
}

#services .title-sub-title {
  font-size: 24px;
  font-family: 'Playfair Display', serif;
  color: rgb(74, 74 , 74);
}

.services-item {
  margin-bottom: 60px;
}

.services-image {
  width: 50%;
  height: 434px;
  resize: both;
  background-repeat: no-repeat;
  background-size: contain;
  display: block;
}

#image-1 {
  right: 0;
  background-image: url('../img/image-1.jpg');
}


#image-2 {
  right: 0;
  background-image: url('../img/image-2.jpeg');
}

#services .hero-image {
  width: 120%;
  height: 900px;
  margin: 80px 0 0 -75px;
  background-image: url('../img/image-3.jpg');
  background-color: #fff;
  background-size: cover;
  background-position: bottom;
  background-repeat: no-repeat;
}

.services-item {
  width: 100%;
  height: 400px;
}

.services-text {
  width: 40%;
  height: 200px;
  margin-top: 7%;
  display: inline-block;
  overflow: hidden;
}

.services-text-title {
  margin-bottom: 30px;
  font-size: 24px;
  font-family: 'Playfair Display', serif;
  color: rgb(74, 74 , 74);
}

.services-text-description {
  font-size: 14px;
  font-family: 'Playfair Display', serif;
  color: rgb(155, 155, 155);
  line-height: 18px;
  text-align: center;
  line-height: 22px;
}

.services-text:first-of-type{
  padding-left: 2%;
}

.services-text:nth-of-type(1) {
  padding-right: 5%;
}

.bump-right {
  padding-left: 5%;
}

.left {
  float: left;
  /* padding: 0 !important; */
}

.right {
  float: right;
  padding: 0 !important;
}


/*  */

@-webkit-keyframes pop {
  0%{
    -webkit-transform: translateY(-100%) rotateX(90deg);
            transform: translateY(-100%) rotateX(90deg);
    opacity: 0;
  }
  100%{
    -webkit-transform: translateY(10%) rotateX(0deg);
            transform: translateY(10%) rotateX(0deg);
    opacity: 1;
  }
}

@keyframes pop {
  0%{
    -webkit-transform: translateY(-100%) rotateX(90deg);
            transform: translateY(-100%) rotateX(90deg);
    opacity: 0;
  }
  100%{
    -webkit-transform: translateY(10%) rotateX(0deg);
            transform: translateY(10%) rotateX(0deg);
    opacity: 1;
  }
}

/* ---------------------------------------MOBILE--------------------------------------- */
@media (min-width: 320px) and (max-width: 734px) {
  #services {
    height: 100%;
    position: relative;
  }

  .title .pre-title-text {
    margin-bottom: 0;
  }

  .pre-title-text {
    margin-bottom: 5px;
    font-size: 10px;
  }

  .title {
    margin-bottom: 24px;
  }

  #services .title-text {
    font-size: 30px;
  }

  .title-text {
    font-size: 28px;
  }

  .services-item {
    position: relative;
    margin-bottom: 30px;
  }

  #services .title-sub-title {
    margin-top: 17px;
    font-size: 17px;
    line-height: 22px;
  }

  .services-text {
    width: 100%;
    height: 200px;
    margin-top: 7%;
    display: inline-block;
    overflow: hidden;
  }
  
  .services-text-title {
    margin-bottom: 23px;
    font-size: 20px;
    font-family: 'Playfair Display', serif;
    color: rgb(74, 74 , 74);
  }
  
  .services-text-description {
    font-size: 12px;
    font-family: 'Playfair Display', serif;
    color: rgb(155, 155, 155);
    line-height: 18px;
    text-align: center;
    line-height: 17px;
  }

  .bump-right {
    margin: 0;
  }

  #image-1 {
    top: 209px;
    left: -6px;
    width: 51vw;
    position: absolute;
  }

  #image-2-pseudo {
    top: 291px;
    left: 142px;
    width: 51vw;
    position: absolute;
    background-image: url('../img/image-2.jpeg');
  }

  #image-2 {
    display: none;
  }

  #services .hero-image {
    width: 106vw;
    height: 254px;
    margin: 0px 0 0 -40px;
  }


}


